<template>
  <user>
    <html-head 
      title="Roles"
    ></html-head>

    <page-header>
      <template v-slot:title>All Roles</template>
    </page-header>

    <template v-if="resources">
      <crud-table-card 
        :api="api" 
        :headers="headers" 
        :optionsChanged="optionsChanged" 
        :resources="resources" 
        name="role"
        :add="true"
        :edit="true"
        :trash="true"
        :view="true"
      ></crud-table-card>
    </template>
  </user>
</template>

<script>
import Api from './routes/api'
import Resources from '@/core/models/Resources'

export default {
  computed: {
    avatarDirectory () {
      return `${ window.location.protocol + "//" + window.location.hostname }:5000/storage/images/avatar/`
    }
  },

  data: () => ({
    api: Api,
    headers: [
      {
        text: 'Name',
        align: 'start',
        value: 'name',
      },
      {
        text: 'Permissions',
        align: 'start',
        value: 'permissions.length',
      },
      {
        text: 'Created',
        align: 'start',
        value: 'createdAt',
      },
      {
        text: 'Actions',
        align: 'end',
        value: 'actions',
      },
    ],

    resources: new Resources
  }),

  methods: {
    optionsChanged ( options ) {
      this.resources.optionsChanged(
        {
          method: 'get',
          url: Api.main(),
          params: {
            search: this.resources.search,
            searchFields: "name",
            options: this.resources.options
          }
        }, 
        options 
      )
    },
  },

  mounted () {
    this.resources.load( false )
  }
}
</script>